.ScheckBox_box__2SIi_ label {
    font-size: 26px;
    color: #4D4D4D;
    position: absolute;
    z-index: 10;
    padding-left: 50px;
    cursor: pointer;
}
.ScheckBox_box__2SIi_ input {
    display: flex;
    flex-wrap: wrap;
    opacity: 0;
    visibility: hidden;
    position: absolute;
}
.ScheckBox_box__2SIi_ input:checked ~ .ScheckBox_check__3oNjM {

    border-color: #00EA90;
    box-shadow: 0px 0px 0px 15px #00EA90 inset;

}
.ScheckBox_box__2SIi_ input:checked ~ .ScheckBox_check__3oNjM::after {

    opacity: 1;
    transform: scale(1);
}
.ScheckBox_box__2SIi_ .ScheckBox_check__3oNjM {
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 100px;
    background-color: #f5f1f1;
    border: 2px solid #30e094;
    box-shadow: 0px 0px 0px 0px #00EA90 inset;
    transition: all 0.15s cubic-bezier(0, 1.05, 0.72, 1.07);

}
.ScheckBox_box__2SIi_ .ScheckBox_check__3oNjM::after {
    content: "";
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 4;
    position: absolute;
    transform: scale(0);
    background-size: 50%;
    background-image: url("http://s6.picofile.com/d/8392306668/c38c12a0-6db3-47d4-a80c-7dad8fab5186/checkmark.svg");
    background-repeat: no-repeat;
    background-position: center;
    transition-delay: 0.2s !important;
    transition: all 0.25s cubic-bezier(0, 1.05, 0.72, 1.07);
}


